import React from 'react';
import validator from 'validator';
import { control } from 'react-validation';
import classnames from 'classnames';

const Input = ({ error, isChanged, isUsed, ...props }) => (
  <div className={classnames({
    "form-control--popmsg": true,
    "form-control--error": isChanged && isUsed && error
  })}>
    <label htmlFor={props.id}>
      {props.label}
      &nbsp;<span className="text-danger">{JSON.parse(props['is-required']) ? '*' : ''}</span>
      &nbsp;&nbsp;{isChanged && isUsed && error}
    </label>
    <input {...props} />
  </div>
);

export const PopEInput = control(Input);

export const required = (value = '') => {
  if (!value.toString().trim().length) {
    return <span className="error">required</span>;
  }
};
 
export const email = (value = '') => {
  if (!validator.isEmail(value)) {
    return <span className="error">{value} is not a valid email.</span>
  }
};
 
