import { USER_LOG_IN } from '~/src/reducers/Login/constants';
import * as api from '~/src/api';

export const login = (inputs) => {
  return (dispatch) => {
    return api
      .fetchUserData(inputs)
      .then(res => {
        // return promise
        return new Promise((resolve, reject) => {
          if (res.error) {
            reject(res);
          }
          else {
            // dispatch redux action
            dispatch({
              type: USER_LOG_IN,
              payload: res
            });
            resolve(res.items);            
          }
        });
      });
  }
}

export const userLoginRedux  = (user) => {
  return (dispatch) => {
    // dispatch redux action
    dispatch({
      type: USER_LOG_IN,
      payload: {
        ...user
      }
    });
    // return promise
    return new Promise((resolve, reject) => {
      resolve(user);
    });
  }
}


