import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { Alert, FormGroup, Button } from 'reactstrap';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as actions from './actions';
import Form from 'react-validation/build/form';
// import Input from 'react-validation/build/input';
import { required, email, PopEInput } from '~/src/method/validator';
import logo from '~/src/assets/images/logo/Shopee-700x217.svg';
import { getCookie, setCookie } from '~/src/method/screen';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLogin: false,
      loginError: false
    };
    this.formSubmit = this.formSubmit.bind(this);
    this.onDismissError = this.onDismissError.bind(this);
  }
  componentDidMount() {
    const { actions } = this.props;
    // getCookie = (cookieName)
    // setCookie = (cookieName, cookieValue, exmins)
    const cookieUser = getCookie('user');
    // if user is in local storage means it's login status
    if (cookieUser) {
      const user = JSON.parse(cookieUser);
      actions
        .userLoginRedux({items: user})
        .then(res =>
          this.setState({ isLogin: true })
        );
    }
    if (localStorage.loginRememberAccount) {
      // console.log(localStorage.loginRememberAccount);
      this.setState({ inputEmail: localStorage.loginRememberAccount });
    }
  }
  formSubmit(event) {
    const { actions } = this.props;
    event.preventDefault();
    // validate all input
    this.form.validateAll();
    // if all inputs are currect
    if (this.form.getChildContext()._errors.length === 0) { 
      // console.log('submit', this.state.inputEmail, this.state.inputPassword);
      // console.log(this.checkboxRememberMe.checked, this.state.inputEmail);
      this.checkboxRememberMe.checked ? localStorage.setItem('loginRememberAccount', this.state.inputEmail) : localStorage.setItem('loginRememberAccount', '');
      // api login
      actions
        .login({
          email: this.state.inputEmail,
          password: this.state.inputPassword
        })
        .then(res => {
          // localStorage.setItem('user', JSON.stringify(res));
          setCookie('user', JSON.stringify(res), 60 * 4);
          this.setState({ isLogin: true });
        })
        .catch(error => {
          this.setState({ loginError: error.message });
        });
    }
  }
  onDismissError() {
    this.setState({ loginError: false });
  }
  render() {
    // console.log(this.props.reduxLogin);
    const rememberMe = localStorage.loginRememberAccount ? localStorage.loginRememberAccount : '';

    if (this.state.isLogin) {

      if (!this.props.history.location.state) {
        return <Redirect to="/" />
      } else {
        const from = this.props.history.location.state.from;
        const prevPath = from.pathname + from.search + from.hash;
        return <Redirect to={prevPath} />
      }

    }
    return (
      [<div className="login" key="1">
        <div className="login__inner">
          <div className="login__logo text-center"><img src={logo} height="55" className="" alt="" /></div>  
          <h1 className="login__title text-center">Shopee Suppliers</h1>
          <div className="login__body">
            <Alert color="danger" isOpen={!!this.state.loginError} toggle={this.onDismissError}>
              {this.state.loginError}
            </Alert>
            <Form ref={c => { this.form = c }} onSubmit={this.formSubmit}>
              <FormGroup>
                <PopEInput type="email"
                  name="email"
                  id="Email"
                  className="form-control"
                  placeholder="Please enter your email."
                  label="Email"
                  is-required="true"
                  value={this.state.inputEmail}
                  onChange={e => this.setState({ inputEmail: e.target.value })}
                  validations={[required, email]} />
              </FormGroup>
              <FormGroup>
                <PopEInput type="password"
                  name="password"
                  id="Password"
                  className="form-control"
                  placeholder="Please enter your password."
                  label="Password"
                  is-required="true"
                  onChange={e => this.setState({inputPassword: e.target.value})}
                  validations={[required]} />
              </FormGroup>
              <FormGroup>
                <div className="custom-control custom-checkbox">
                  <input type="checkbox" className="custom-control-input" id="rememberMe" defaultChecked={!!rememberMe} ref={c => { this.checkboxRememberMe = c }} />
                  <label className="custom-control-label" htmlFor="rememberMe">Remember me.</label>
                </div>
              </FormGroup>
              <Button type="submit" className="btn-info btn-block">Submit</Button>
            </Form>
          </div>
        </div>
      </div>,
      <div className="login__backdrop" key="2"></div>]
    );
  }
}

const mapStateToProps = (state, ownProps) => ({
  ...state
});

const mapDispatchProps = (dispatch, ownProps) => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchProps
)(Login);


